.login-page {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    background-color: #301934;
}

.login-container{
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 300px;
}
.login-container h2 {
    margin-bottom: 20px;
    text-align: center;
}
.login-container .form-group {
    margin-bottom:  15px;
}

.login-container .form-group label {
    display: block;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px;
}

.login-container .form-group input, 
.login-container .form-group select {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
    margin-top: 5px;
}
.login-container .btn-login {
   background-color: #4caf50;
   color: #fff;
   padding: 10px;
   width: 100%;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   font-weight: bold;
   margin-top: 8px;
}